.hero {
  padding: 10em 0 7em 2em;
  position: relative;
}

.hero h1 {
  font-size: 4em;
  position: relative;
  z-index: 10;
}

.hero p {
  position: relative;
  padding-left: 2em;
  z-index: 10;
}

.hero-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  max-width: 750px;
  margin: 4em 0 0 2em;
}

.hero-buttons button {
  position: relative;
  z-index: 10;
  min-width: 350px;
}

.hero-buttons button::after {
  content: '→';
  position: absolute;
  top: 1em;
  right: 1em;
}

.illo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
}

.illo .big-star {
  width: 400px;
  height: 400px;
  border-radius: 50%;
  background: #ffd413;
  -webkit-box-shadow: 0 0 50px 30px #ffd413;
          box-shadow: 0 0 50px 30px #ffd413;
  position: absolute;
  top: 6em;
  right: 1em;
  z-index: 4
}

.illo .big-star::after {
  content: '';
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  bottom: -20px;
  left: 10px;
  background: #ff3d2d;
  -webkit-box-shadow: 0 0 20px 10px #ff3d2d;
          box-shadow: 0 0 20px 10px #ff3d2d;
  z-index: 3
}

.illo .perspective-box {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%;
  border: 2px solid coral;
  -webkit-transform: perspective(400px) rotateX(47deg);
          transform: perspective(400px) rotateX(47deg);
  z-index: 1;
}

@media (max-width: 850px) {
  .hero {
    padding: 5em 0 0 0em;
  }

  .hero h1 {
    font-size: 2em;
  }

  .hero p {
    padding-left: 1em;
  }

  .hero-buttons {
    margin-left: 1em;
    margin-top: 3em;
  }

  .hero-buttons button {
    min-width: 300px;
  }

  .illo .big-star {
    right: -8em;
    width: 300px;
    height: 300px;
  }

  .illo .big-star::after {
    width: 80px;
    height: 80px;
  }

  .illo .perspective-box {

  }
}


