.footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 8em 0;
  padding: 0 2em;
}

.first button {
  width: 70%;
}

.first p {
  margin-top: 0;
}

.first {
  -ms-flex-positive: 3;
      flex-grow: 3;
}

.second {
  width: 20%;
}

.second button {
  width: 100%;
  padding: 0.5em;
  margin-bottom: 0;
  border-bottom: 0
}

.second button:last-child {
  border-bottom: 1px solid #222;
}

.second button:after {
  content: '↗';
  top: 0.5em;
}

@media (max-width: 850px) {
  .first button {
    width: 100%;
  }

  .second {
    width: 100%;
  }
}


